    body {
      padding-top: 126px; }
      body #page {
        overflow-x: hidden; } }

.wrapper {
  padding-top: 0px;
  padding-bottom: 0px;
  margin-left: 9px;
  margin-right: 9px; }

@media (max-width: 1457.98px) {
  .navbar .navbar-container, #content, .page-section__content-wrapper {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px; } }

@media (min-width: 1458px) {
